@import '../../mixin.scss';

.landing {
	display: flex;
}

.info {
	width: calc(50% - 80px);
	height: calc(100vh - 80px);
	padding: 40px;
	border-right: 1px solid #E1E1E1;
	position: relative;
}

.intro {
	max-width: 320px;
	font-size: 14px;
	line-height: 1.25em;
}

.nav {
	position: absolute;
	top: 38%;
	margin-left: -10px;

	ul { 
		list-style-type: none;
		padding: 0;

		li {
			margin: 14px 0;
			cursor: pointer;
		}

		li:first-child {
			margin-top: 0;
		}

		li:last-child {
			margin-bottom: 0;
		}

		a { 
			padding: 0 10px;
			font-size: 20px;
		}
	}
}

.footer {
	position: absolute;
	bottom: 40px;
}

.images {
	width: calc(50% - 80px);
	padding: 40px;
	position: relative;
	display: flex;
    align-items: center;
}

.carousel {
	flex-direction: column;
}

.projectImage {
	// margin-top: -32px;
	z-index: 5;
    position: absolute;
    top: 18%;
    margin-top: 51px;
    width: calc(100% - 80px);
    
	img {
		width: 100%;
	}
}

@media only screen and (max-width: 600px) {
	.landing {
		flex-direction: column;
	}

	.info, .images {
		width: unset;
		height: unset;
		padding: 32px;
	}

	.nav {
		position: static;
		top: unset;

		ul {
			li {
				margin: 10px 0;

				a {
					font-size: 18px;
				}
			}
		}
	}

	.footer {
		position: static;
		bottom: unset;
		font-size: 12px;
	}
}
