@import '../../mixin.scss';


.randomizer {
	display: none;
	background-image: url("../../assets/images/randomizer.png");
	width: 100px;
	height: 100px;
	background-size: contain;
	@include animate(spin, 20s, infinite, none, linear);
	position: absolute;
	bottom: 40px;
	right: 40px;
}

@include keyframes (spin) {
	0% { transform:rotate(0deg); }
	100% { transform:rotate(360deg); }
}

@media only screen and (max-width: 600px) { 
	.randomizer {
		display: none;
	}
}